const AJAX = new XMLHttpRequest()
const CONTAINER = document.getElementById('root')
const CONTENT = document.createElement('div')
const NEWS_URL = 'https://api.hnpwa.com/v0/news/1.json'
// Ïã§Ï†ú ÏÇ¨Ïö©ÏûêÍ∞Ä ÌÉÄÏù¥ÌãÄÏùÑ ÌÅ¥Î¶≠ÌñàÏùÑ Îïå, CONTENTS_URLÏùÑ Í∞ÄÏßÄÍ≥† AJAX Ìò∏Ï∂úÏùÑ ÌïòÏó¨ Îç∞Ïù¥ÌÑ∞Î•º Í∞ÄÏ†∏Ïò§Ïûê.
// Ïù¥Î≤§Ìä∏ ÏãúÏä§ÌÖúÏùÄ Î∏åÎùºÏö∞Ï†ÄÍ∞Ä Ï†úÍ≥µÌïúÎã§.
const CONTENT_URL = 'https://api.hnpwa.com/v0/item/@id.json'
const STORE = {
    currentPage: 1,
}

function getData(url) {
    AJAX.open('GET', url, false) // false : ÎèôÍ∏∞Ï†ÅÏúºÎ°ú Ï≤òÎ¶¨ÌïòÍ≤†Îã§Îäî ÏòµÏÖò
    AJAX.send()

    return JSON.parse(AJAX.response)
}
function newsFeed() {
    // Î©îÏù∏ ÌéòÏù¥ÏßÄ
    const NEWSFEED = getData(NEWS_URL)
    const NEWSLIST = []

    let template = `
        <div class="container mx-auto p-4">
            <h1>HACKER NEWS</h1>
            <ul>
                {{__news_feed__}}
            </ul>
            <div>
                <a href="#">{{__prev_page__}}</a>
                <a href="#">{{__next_page__}}</a>
            </div>
        </div>
    `

    for (let i = (STORE.currentPage - 1) * 10; i < STORE.currentPage * 10; i++) {
        NEWSLIST.push(`
        <li>
            <a href="#/show/${NEWSFEED[i].id}">
                ${NEWSFEED[i].title} (${NEWSFEED[i].comments_count}) 
            </a> 
        </li>
    `)
    }

    template = template.replace('{{__news_feed__}}', NEWSLIST.join(''))
    template = template.replace('{{__prev_page__}}', STORE.currentPage > 1 ? STORE.currentPage - 1 : 1)
    template = template.replace('{{__next_page__}}', STORE.currentPage + 1)

    CONTAINER.innerHTML = template
}
function newsDetail() {
    const id = location.hash.substring(7) // ÎÇ¥Í∞Ä Ïì∞Í≥† Ïã∂ÏùÄ ÏúÑÏπò Í∞íÏùÑ ÏßÄÏ†ïÌï¥Ï£ºÎ©¥ ÎêúÎã§. Í∑∏ Ïù¥ÌõÑÏùò ÎÇòÎ®∏ÏßÄ Î¨∏ÏûêÏó¥Îì§Îßå Î∞òÌôòÌïúÎã§.
    const NEWSCONTENT = getData(CONTENT_URL.replace('@id', id))

    CONTAINER.innerHTML = `
        <h1>${NEWSCONTENT.title}</h1>

        <div>
            <a href="#/page/${STORE.currentPage}">Î™©Î°ùÏúºÎ°ú</a>
        </div>    
    `
}
function router() {
    const routePath = location.hash

    // location.hashÍ∞Ä #Îßå ÏûàÏùÑ Í≤ΩÏö∞ÏóêÎäî Îπà Í∞íÏùÑ Î∞òÌôòÌïúÎã§.
    // Îî∞ÎùºÏÑú trueÎ•º Î∞òÌôòÌïúÎã§.
    if (routePath === '') {
        newsFeed()
    } else if (routePath.indexOf('#/page/') >= 0) {
        STORE.currentPage = Number(routePath.substring(7))
        newsFeed()
    } else {
        newsDetail()
    }
}

window.addEventListener('hashchange', router)

router()
